import { Card, Grid, styled, useTheme, Icon, IconButton } from '@mui/material';
import { Fragment, useEffect, useState } from 'react';
import Campaigns from './shared/Campaigns';
import DoughnutChart from './shared/Doughnut';
import RowCards from './shared/RowCards';
import StatCards from './shared/StatCards';
import StatCards2 from './shared/StatCards2';
import TopSellingTable from './shared/TopSellingTable';
import UpgradeCard from './shared/UpgradeCard';
import KeyValueCards from 'app/views/dashboard/shared/KeyValueCards';
import { useSelector } from 'react-redux';
import Parse from 'app/vendor/Parse';
import { ca } from 'date-fns/locale';
import NumberConsole from 'app/components/NumberConsole'

const ContentBox = styled('div')(({ theme }) => ({
  margin: '30px',
  [theme.breakpoints.down('sm')]: { margin: '16px' },
}));

const Title = styled('span')(() => ({
  fontSize: '1rem',
  fontWeight: '500',
  marginRight: '.5rem',
  textTransform: 'capitalize',
}));

const SubTitle = styled('span')(({ theme }) => ({
  fontSize: '0.875rem',
  color: theme.palette.text.secondary,
}));

const H4 = styled('h4')(({ theme }) => ({
  fontSize: '1rem',
  fontWeight: '500',
  marginBottom: '16px',
  textTransform: 'capitalize',
  color: theme.palette.text.secondary,
}));



const Analytics = ({ itemKey }) => {
  const { palette } = useTheme();
  const [items, setItems] = useState([])
  const [itemMeta, setItemMeta] = useState([])


  const handleDelete = async (row) => {
    await Parse.Cloud.run('deleteItems', {
      ids: [row?.objectId]
    })
  }
  const columns = [
    {
      header: 'Key',
      render: row => row?.type
    },
    {
      header: 'Updated',
      render: row => row?.updatedAt
    },

    {
      header: 'Action',
      render: row => <>
        <IconButton>
          <Icon color="primary">search</Icon>
        </IconButton>
        <IconButton>
          <Icon color="primary">edit</Icon>
        </IconButton>
        <IconButton onClick={() => handleDelete(row)}><Icon color="primary">delete</Icon></IconButton>

      </>
    }
  ]


  const [tagData, setTagData] = useState({})


  useEffect(async () => {
    const res = await Parse.Cloud.run('getItemsMeta', {
      key: itemKey
    })
    console.log(res[0])
    setItemMeta(res[0])
    setItems(res)
  }, [])

  return (
    <Fragment>
      <ContentBox className="analytics">
        <Grid container spacing={3}>
          <Grid item lg={12} md={12} sm={12} xs={12}>
            <KeyValueCards
              itemKey={itemKey}
            />
          </Grid>
          <Grid item lg={12} md={12} sm={12} xs={12}>

            <StatCards
              onClickDetails={(card) => {
                setTagData(card?.item)
              }}
              cardList={itemMeta?.tag ? Object.keys(itemMeta?.tag).map(k => ({
                name: k,
                amount: Array.isArray(itemMeta?.tag[k]) ? itemMeta?.tag[k].length : itemMeta?.tag[k],
                item: itemMeta?.tag[k]
              })) : []}
            />
            <NumberConsole
              dataArr={tagData}
              pageSize={12}
              mask={'yyyyyyyy'}
              displaySource
            />
            {
              <TopSellingTable
                rows={items}
                columns={columns}
              />
            }
          </Grid>

        </Grid>
      </ContentBox>
    </Fragment>
  );
};

export default Analytics;
